.pagination{
	&__number{
		display: inline-block;
		font-size: 1.6rem;
		padding: 1rem;
		color: $color-secondary;
		border: 0.1rem solid $color-secondary;
		cursor: pointer;
		margin: 0.1rem;
		user-select: none;

		@include respond (phone){
			font-size: 0.6rem;
		}

		&--active{
			color: $color-primary;
			background-color: $color-secondary;
		}

		&:hover{
			color: $color-primary;
			background-color: $color-secondary;
		}
	}
	
}