.auth {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	&__title{
		font-size: 4.2rem;
		color: $color-link-hover;
		margin-top: 4rem;
		margin-bottom: 1rem;
	}

	&__lead{
		font-size: 2.4rem;
		color: $color-grey-light;
		margin-bottom: 2rem;
	}

	&__form{
		&__group{
			margin-bottom: 1rem;
		}

		&__input{
			font-size: 1.6rem;
			padding: 1rem 1.5rem;
			outline-color: $color-secondary;
		}

		&__button{
			font-size: 1.8rem;
			padding: 0.5rem 0;
			width: 80%;
			margin-left: 10%;
			background-color: $color-green;
			color: $color-secondary;
			border: 0.1rem solid $color-primary;
			border-radius: 0.5rem;
			cursor: pointer;
			margin-top: 1rem;
			margin-bottom: 2rem;
			outline: none;

			&:hover{
				color: $color-green;
				background-color: $color-secondary;
			}
		}
	}

	&__authtext{
		font-size: 1.6rem;
		color: $color-paragraph;
		margin-bottom: 5px;

		&__link{
			text-decoration: none;
			color: $color-secondary;
		}

		&:hover{
			color: $color-link-hover;
		}

		@include respond(phone){
			margin-bottom: 50px;
		}
	}
}