@mixin clearfix {
	&::after{
		content: "";
		display: table;
		clear: both;
	}
}

@mixin absCenter{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%)
}

@mixin respond($breakpoint){
	@if $breakpoint == phone {
		@media only screen and(max-width: 37.5em){@content};  //600px
	}
	@if $breakpoint == tab-port {
		@media only screen  and(maxwidth: 56.25em){@content};  //900px
	}
	@if $breakpoint == tab-land {
		@media only screen  and(maxwidth: 75em){@content};	//1200px
	}
	@if $breakpoint == big-desktop {
		@media only screen  and(maxwidth: 112.5em){@content};  //1800px
	}
}