.navbar {
	width: 100%;

	&__top{
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 10rem;
		background-color: $color-primary;

		&__logo {
			margin-left: 4rem;

			&__link {
				font-size: 2.8rem;
				font-weight: bold;
				color: $color-link-hover;
				text-decoration: none;
			}

			img{
				width: 200px;
			}

			@include respond(phone) {

				&__link{
					font-size: 1.8em;
				}
			}
		}

		&__auth {
			margin-right: 4rem;

			&__link {
				font-size: 1.8rem;
				color: $color-secondary;
				text-decoration: none;	
				
				&:first-of-type{
					margin-right: 8rem;	
				}

				&:hover{
					color: $color-link-hover;
				}
			}

		}
	}

	&__bottom {
		display: flex;
		justify-content: space-around;
		align-items: center;
		width: 100%;
		height: 7rem;	
		background-color: $color-tertiary;
		box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
		list-style: none;

		&__item {
			list-style: none;
			font-size: 1.8rem;
			color: $color-secondary;
			text-decoration: none;
			width: 100%;
			height: inherit;
			text-align: center;
			padding-top: 2rem;

				&:hover {
					font-weight: bold;
					font-size: 2.2rem;
					color:$color-quaternary;
					background-color: $color-secondary;
				}	
		}
		.active {
			color: $color-secondary;
			font-weight: bold;
			font-size: 2.2rem;
			background-color: $color-quaternary;
		}

	}
}
