.about {
	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 8rem 0;
		background-color: rgba($color-secondary, 0.45);
	}

	&__info {
		margin-top: 4rem;
	}

	&__team {
		margin-top: 4rem;
		margin-bottom: 6rem;
	}

	&__heading {
		font-size: 4.2rem;
		color: $color-secondary;
		font-weight: 200;

		@include respond(phone) {
			font-size: 2.8rem;
		}
	}

	&__subheading {
		font-size: 3.2rem;
		color: $color-secondary;
		font-weight: 200;
		margin-bottom: 2rem;

		@include respond(phone) {
			font-size: 1.5rem;
		}
	}

	&__paragraph {
		font-size: 1.6rem;
		color: $color-paragraph;
		margin-top: 1rem;
		margin-bottom: 1rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}

	&__display {
		width: 100%;

		&__image {
			width: 100%;
		}
	}

	&__realtor {
		font-size: 1.8rem;
		font-weight: 200;
		color: $color-secondary;
		margin-top: 1rem;
		margin-bottom: 1rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}

	&__topseller {
		font-size: 2.4rem;
		color: $color-paragraph;
		font-weight: 200;
		margin-top: 2rem;

		@include respond(phone) {
			font-size: 1.5rem;
		}
	}

	&__contact {
		font-size: 1.6rem;
		color: $color-secondary;
		margin-bottom: 0.5rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}

	&__about {
		font-size: 1.6rem;
		color: $color-paragraph;
		margin-top: 1rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}
}