*,
*::after,
*::before{
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

html{
	font-size: 62.5%;

	@include respond(tab-land){
		font-size: 56.25%;
	}

	@include respond(tab-port) {
		font-size: 50%;
	}

	@include respond(tab-phone) {
		font-size: 80%;
	}
}

body{
	box-sizing: border-box;
	font-family: cursive, bebas-neue, sans-serif;
	background-color: $color-primary;

	@include respond(tab-port) {
		padding: 0;
	}
}

::selection {
	background-color: rgba($color-red, 0.3);
}