.card {
	padding:4rem 3rem;
	background-color: $color-quaternary;
	box-shadow: 10px 10px 10px  rgba(0, 0, 0, 0.5);
	border-radius: 0.5rem;

	@include respond(phone){
		width: 130px;
		height:250px;
		padding-left: 4px;
		padding-top: 0px;
	}

	&__header{
		width: 100%;

		&__photo {
			width: 100%;
		}
	}

	&__title {
		color: $color-primary;
		text-shadow: 1px 1px 0 #000;
		font-size: 3rem;
		margin-bottom: 2rem;

		@include respond(phone){
			font-size: 1.5rem;
			margin-bottom: 1rem;
		}
	}

	&__location {
		font-size: 1.4rem;
		margin-top: 2rem;
		margin-bottom: 2rem;
		color: $color-link-hover;

		@include respond(phone) {
			font-size: 0.7rem;
			margin-top: 1rem;
			margin-bottom: 1rem;
		}
	}

	&__info {
		font-size: 1.6rem;
		font-weight: bold;
		text-shadow: 5px 4px 4px rgba(0, 0, 0, 0.5);
		color: $color-secondary;
		margin-bottom: 0.5rem;

		@include respond(phone) {
			font-size: 0.8rem;
			margin-left: 0px;
		}
	}

	&__saletype {
		font-size: 1.6rem;
		font-weight: bold;
		color: $color-green;
		margin-bottom: 0.5rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}

	&__hometype {
		font-size: 1.6rem;
		color: rgb(238, 169, 41);
		margin-bottom: 0.5rem;

		@include respond(phone) {
			font-size: 0.8rem;
		}
	}

	&__sqft {
		font-size: 1.2rem;
		color: $color-secondary;

		@include respond(phone) {
			font-size: 0.6rem;
		}

	}

	&__link {
		font-size: 1.6rem;
		color: $color-secondary;
		padding: 1rem 1.5rem;
		border: 0.1rem solid $color-secondary;
		text-decoration: none;

		@include respond(phone) {
			font-size: 0.6rem;
		}

		&:hover {
			color: $color-quaternary;
			border: 0.1rem solid $color-secondary;
			background-color: $color-secondary;
		}
	}
}