.listingform{
	&__section{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;

		@include respond(phone) {
			display: inline-block;
		}

	}

	&__altsection {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 1.5rem;
		
		@include respond(phone) {
			
		}
	}

	&__label {
		font-size: 1.8rem;
		color: $color-secondary;
		margin-bottom: 0.8rem;

		@include respond(phone) {
			font-size: 1rem;
				
		}
	}

	&__select{
		font-size: 1.6rem;
		width: 100%;
		height: 3rem;
		outline: none;
		margin-bottom: 1.5rem;
		border-radius: 4rem;
		background-color: $color-primary;
		color: white;	

		@include respond(phone) {
			font-size: small;
			width: 70px;
			margin-bottom: 1.5rem;	
		}
	}

	#inpt {
		width: 100%;
		height: 3rem;
		font-size: 1.6rem;
		padding: 0.5rem 0.8rem;
		outline: none;
		border-radius: 4rem;
		color:$color-secondary;
		background-color: $color-primary !important;
	}

	&__checkbox {
	    	margin-right: 1rem;
	    	margin-bottom: 0.5rem;
	}

	&__button {
		padding: 1rem 2rem;
		font-size: 1.8rem;
		font-weight: bold;
		color: $color-secondary;
		background-color: $color-primary;
		border: 2px solid $color-secondary;
		margin-top:  2rem;
		cursor: pointer;

		&:hover{
			background-color: $color-secondary;
			color: $color-primary;
		}

		@include respond(phone) {
			margin-top: 10px;
			margin-left: 160px;
			font-size: 1rem;
		}
	}

	&__loader {
		padding-top: 2rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}


.switchBox {
	margin-left: 20px;
	width: 60px;
	height: 25px;
	background: #d8d8d8;
	border-radius: 20px;
	position: relative;
}

.switchBox input {
	width: 100%;
	height: 100%;
	cursor: pointer;
	position: absolute;
	z-index: 100;
	-webkit-appearance: none;
}

.switchBox .box {
	position: absolute;
	top: 10%;
	left: 4px;
	width: 20px;
	height: 80%;
	background: #6d6d6d;
	border-radius: 20px;
	transition: all .4s;
}

.switchBox input:checked~.box {
	left: 60%;
	background: $color-link-hover;
}
